import React, { useState, useEffect } from 'react';
import { API, Auth } from 'aws-amplify';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { Loader } from '../../components/Default/Loader';

const Wrapper = styled.div``;

const StyledTitle = styled.h1``;

const TripsList = ({ isLoggedIn }) => {
  const [isLoading, setIsLoading] = useState(true);
  const [trips, setTrips] = useState([]);

  useEffect(() => {
    if (!isLoggedIn) {
      return;
    }

    loadListOfTrips();
    console.log(trips);
  }, []);

  const loadListOfTrips = async () => {
    try {
      console.log({ user: await Auth.currentAuthenticatedUser() });
      console.log({ API, Auth });
      const trips = await API.get('trips', '/trips');
      setTrips(trips);
    } catch (e) {
      alert(e);
    }

    setIsLoading(false);
  };

  const renderTripsList = (trips) => {
    return (
      <div>
        {trips.map((trip) => (
          <>
            <span>trip.name</span>
            <span>trip.note</span>
            <span>trip.date_from</span>
            <span>trip.date_to</span>
            <span>trip.rating</span>
          </>
        ))}
      </div>
    );
  };

  return (
    <Wrapper>
      <StyledTitle>Your trips</StyledTitle>
      <Link to="/trips/new">New trip</Link>
      {isLoading ? <Loader /> : renderTripsList(trips)}
    </Wrapper>
  );
};

export default TripsList;
